.grid {
  height: 70vh;
  width: 100vw;
  border-radius: 20px;
  display: grid;
  grid-template-columns: 45% 55%;
  /* grid-template-columns: repeat(2, 50%); Adjust width as needed */
  grid-template-rows: repeat(1, 100%); /* Adjust height as needed */
}

.main-grid {
  background-color: transparent;
  border-radius: 0px;
  background-image: url("../Res/abstract.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}



#About-Us h1,
#About-Us h2,
#About-Us h3,
#About-Us h4 {
  position: relative;
  color: white;
  text-align: start;
  padding-right: 200px;
  padding-bottom: 40px;
  z-index: 2;
  
}


#About-Us h3{
  font-size: 35px;
}

.above{
  z-index: -1;
}
#About-Us h1 {
  font-weight: 900;
}

#About-Us {
  padding-top: 40px;
  height: 70%;
  
}

.big {
  font-size: 40px;
}

.dsop {
  visibility: hidden;
}

#Phone-Img {
  height: 45vh;
  width: 15vw;
  background-image: url("../Res/hands-holding-question-mark-symbol.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 20px;
  box-shadow: 10px 10px 0px 0px rgb(255, 255, 255, 0.23);
}

#centre {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#phone {
  z-index: 1;
  position: absolute;
  color: red;
  right: 10vw;
  height: 20vh;
  width: 10vw;
  background-image: url("../Res/phone.png");
  background-size: cover;
  background-repeat: no-repeat;
}

