@media (max-width: 1768px) {

  .grid {
    height: 70vh;
    width: 100vw;
    border-radius: 20px;
    display: grid;
    grid-template-columns: 60% 40%;
    /* grid-template-columns: repeat(2, 50%); Adjust width as needed */
    grid-template-rows: repeat(1, 100%); /* Adjust height as needed */
  }
  #About-Us h1,
  #About-Us h2,
  #About-Us h3,
  #About-Us h4 {
    color: white;
    text-align: start;
    padding-right: 6px;
    padding-bottom: 12px;
  }

  #phone {
    visibility: hidden;
  }

  #Phone-Img {
    height: 70vh;
    width: 25vw;
    background-image: url("../Res/hands-holding-question-mark-symbol.jpg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 20px;
    box-shadow: 10px 10px 0px 0px rgb(255, 255, 255, 0.23);
  }

  .big{
    font-size: 25px;
  }
  #About-Us h3{
    font-size: 20px;
  }

}



@media (max-width: 1280px) {

  #Phone-Img {
    height: 50vh;
    width: 40%;
    background-image: url("../Res/hands-holding-question-mark-symbol.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 20px;
    box-shadow: 10px 10px 0px 0px rgb(255, 255, 255, 0.23);
  }

  .big{
    font-size: 25px;
  }
  #About-Us h3{
    font-size: 20px;
  }

}



@media (max-width: 480px) {

 
  #About-Us h3{
    font-size: 18px;
  }
  
  .main-grid {
    height: 100%;
    background-color: transparent;
    border-radius: 0px;
    background-image: url("../Res/abstract.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  .why-main {
    width: 100%;
    height: 90%;
  }
  
  .grid {
    
    height: 70%;
    width: 100%;
    /* box-shadow: 0 0 30px rgba(0, 0, 0, 0.3); */
    border-radius: 0px;
    display: flex;
    /* display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: repeat(1, 75%);
    background-color: black; */
    /* padding-bottom: 50px; */
    
    overflow: scroll;
  }
  .dsop {
    visibility: visible;
  }
  #About-Us h1,
  #About-Us h2,
  #About-Us h3,
  #About-Us h4 {
    color: white;
    text-align: start;
    padding-right: 6px;
    padding-bottom: 12px;
  }



  #About-Us h1 {
    font-weight: 900;
  }

  #About-Us {
    padding-top: 20px;
    width: 50%;
    height: 0%;
  }

  h4 {
    font-size: 10px;
  }

  br .remove {
    display: none;
    
  }

  .big {
    display: none;
  }

  #Phone-Img {
    height: 30vh;
    width: 90%;
    background-image: url("../Res/hands-holding-question-mark-symbol.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 20px;
    box-shadow: 10px 10px 0px 0px rgb(255, 255, 255, 0.23);
  }

  #centre {
    padding: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .grid-sub {
    height: 70%;
    width:50%;
  }
  .red {
    height: 100%;
  }

  .dnsop {
    visibility: hidden;
  }

  .dsop {
    width: 100%;
    
  }

  #phone {
    z-index: -1;
    
  }
}
