#sec {
  height: 100vh;
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

#secc {
  height: 70vh;
  width: 100vw;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.primary {
  background-color: black;
}

.headline {
  margin-left: 25px;
  font-size: 65px;
  font-weight: 700;
  color: white;
  text-align: left;
  align-self: left;
}

.sub {
  margin-left: 25px;
  font-size: 25px;
  font-weight: 100;
  color: white;
}

h3.sub {
  width: 100%;
  margin-left: 25px;
  font-size: 45px;
  font-weight: 500;
  color: #F06192;
}

button {
  background-color: #F06192;
  color: white;
  border-radius: 30px;
  padding: 10px;
  margin-left: 25px;
  margin-top: 20px;
  font-size: 20px;
  font-weight: 500;
  text-align: left;
  align-self: left;
  transition: 0.5s linear;
  border: none;
}

button:hover {
  background-color: white;
  color: #F06192;
}

.sally {
  z-index: 0;
  position: absolute;
  background-image: url("../Res/world-desktop.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 40vw;
  height: 70vh;
  right: 10%;
}

@media (max-width: 1768px) {
  /* Your CSS rules for tablet screens here */
  /* You can reduce font sizes, change layouts, or hide/show elements */
  .headline {
    margin-left: 25px;
    font-size: 55px;
    font-weight: 700;
    color: white;
    text-align: left;
    align-self: left;
  }
  .pad {
    padding-top: 30px;
  }
  .sub {
    margin-left: 25px;
    font-size: 24px;
    font-weight: 100;
    color: white;
  }

  h3.sub {
    width: 100%;
    margin-left: 25px;
    font-size: 35px;
    font-weight: 500;
    color: #F06192;
  }

  button {
    background-color: #F06192;
    color: white;
    border-radius: 30px;
    padding: 10px;
    margin-left: 25px;
    margin-top: 20px;
    font-size: 25px;
    font-weight: 500;
    text-align: left;
    align-self: left;
    transition: 0.5s linear;
    border: none;
  }
  .sally {
    z-index: 0;
    position: absolute;
    background-image: url("../Res/phone-Saly-13.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 40vw;
    height: 100%;
    right: -0px;
    top: -80px;
  }
  .top {
    z-index: 1;
  }
  #sec {
    height: 100vh;
    width: 100vw;
  }

  #secc {
    height: 100vh;
    width: 100vw;
  
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
  }
}

