#stats-box {
  height: 16vh;
  width: 35vw;
  background-color: #f06192;
  border-radius: 20px;
  color: white;
}

.single {
  width: 300px;
}

.main-container {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  display: grid;
  /* grid-template-columns: 40% 60%; */
  grid-template-columns: repeat(3, 33.33%);
  grid-template-rows: repeat(1, 100%);
  height: 100%;
}

.sub-container {
  height: 80%;
  display: flex;
  align-self: center;
  justify-content: center;
  padding-left: 5%;
  padding-right: 5%;
}

.sub-container h1 {
  font-size: 34px;
  font-weight: 900;
  color: white;
  padding: 0;
  margin: 0;
}

.sub-container h4 {
  text-align: center;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  color: white;
  padding: 0;
  margin: 0;
}

@media (max-width: 1768px) {
  #stats-box {
    height: 10vh;
    width: 100%;
    background-color: #2d1717;
    border-radius: 20px;
    color: white;
    box-shadow: 10px 10px 0px 0px rgba(0, 0, 0, 0.23);
  }

  .sub-container h4 {
    font-size: 10px;
  }

  .sub-container h1 {
    font-size: 24px;
  }
}


@media (max-width: 1280px) {
  .sub-container h4 {
    font-size: 10px;
  }

  .sub-container h1 {
    font-size: 24px;
  }

}



@media (max-width: 480px) {

  .sub-container {
    height: 70%;
    display: flex;
    align-self: center;
    justify-content: center;
    padding-left: 5%;
    padding-right: 5%;
  }

  #stats-box {
    height: 14vh;
    width: 90vw;
    background-color: #2d1717;
    border-radius: 20px;
    color: white;
    box-shadow: 10px 10px 0px 0px rgba(0, 0, 0, 0.23);
  }
 

  .sub-container h4 {
    font-size: 10px;
  }

  .sub-container h1 {
    font-size: 24px;
  }
}
