.feedback-input {
  color: black;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 18px;
  border-radius: 5px;
  line-height: 22px;
  background-color: transparent;
  border: 2px solid #f06192;
  transition: all 0.3s;
  padding: 13px;
  margin-bottom: 15px;
  width: 100%;
  box-sizing: border-box;
  outline: 0;
}

div {
  color: black;
}
.feedback-input:focus {
  border: 2px solid #cc4949;
}

textarea {
  height: 10vh;
  line-height: 20%;
  resize: vertical;
  resize: none;
}

[type="submit"] {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  width: 100%;
  background: #f06192;
  border-radius: 5px;
  border: 0;
  cursor: pointer;
  color: white;
  font-size: 24px;
  padding-top: 10px;
  padding-bottom: 10px;
  transition: all 0.3s;
  margin-top: -4px;
  font-weight: 700;
}
[type="submit"]:hover {
  background: #cc4949;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: url("/src/Res/bg-lvl.png");
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.sub {
  width: 70%;

  color: white;
}
.sub-two {
  width: 50%;
  color: white;
}
.contact-us {
  width: 50%;
  height: 20%;
  padding: 15px;
  background-color: #ffffff;
  border-radius: 15px;
}

.form-bg {
  height: 100%;
  padding: 15px;
  width: 100%;
  background-color: black;
}

.main-head {
  color: white;
  font-weight: 700;
  font-size: 60px;
}

.yellow {
  color: #f06192;
}

.sub-head {
  color: white;
  font-size: 24px;
}

.msg {
  color: white;
  font-size: 26px;
  width: 40%;
  font-weight: 700;
}

.booking {
  font-weight: 600;
  color: black;
}

@media (max-width: 1768px) {
  .main-head {
    font-weight: 700;
    font-size: 48px;
  }
  .msg {
    display: none;
  }

  .main {
    display: block;
  }

  .sub-head {
    padding-bottom: 25px;
    font-size: 22px;
  }

  .sub-two {
    padding-left: 15px;
    width: 100%;
    color: white;
  }

  .contact-us {
    width: 30%;
    height: 20%;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 15px;
  }

  .smaller-in-phone {
    font-size: 18px;
  }

  .smaller {
    font-size: 22px;
  }

  .form-bg {
    height: 100%;
    padding: 0px;
    width: 100%;
    background-color: black;
  }

  textarea {
    height: 10vh;
    line-height: 20%;
    resize: vertical;
    resize: none;
  }

  .main {
    height: 100%;

    background-image: url("/src/Res/bg-lvl.png");
    background-position: right bottom;
  }
}

@media (max-width: 480px) {
  .main-head {
    font-weight: 700;
    font-size: 28px;
  }
  .msg {
    display: none;
  }

  .main {
    display: block;
  }

  .sub-head {
    font-size: 14px;
  }

  .sub-two {
    padding-left: 15px;
    width: 100%;
    color: white;
  }

  .contact-us {
    width: 90%;
    height: 20%;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 15px;
  }

  .smaller-in-phone {
    font-size: 12px;
  }

  .smaller {
    font-size: 16px;
  }

  .form-bg {
    height: 100%;
    padding: 15px;
    width: 100%;
    background-color: black;
  }

  textarea {
    height: 10vh;
    line-height: 20%;
    resize: vertical;
    resize: none;
  }
}

.form-bg {
  border: none;
}
