@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");

:root {
  --primary-color: #F06192;
  --secondary-color: white;
}




* {
  /* border: solid 1px red; */
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", "inter", sans-serif;
  overflow-x: hidden;
}

.certified {
  margin: 15px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  align-items: center;
  justify-content: center;
}

h1 {
  font-size: 25px;
  font-weight: 500;
  text-align: center;
  color: white;
}

#Boxcontainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 60vw;
  height: 16vh;
  background-color: white;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.image {
  height: 80%;
  align-items: center;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.one {
  background-image: url("./Res/Logo_University_of_Helsinki_fi.svg.png");
}

.two {
  background-image: url("./Res/google-logo.jpg");
}

.three {
  background-image: url("./Res/1667px-Figma-logo.svg.png");
}

.center-container {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bg-graffiti {
  background-image: url("./Res/bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.inner-shadow {
  box-shadow: inset 0px 0px 400px 200px rgba(255, 255, 255, 1);
}

.curved-section {
  border-bottom-left-radius: 20px;
  position: relative;
}

.show {
  visibility: hidden;
  height: 0;
  width: 0;
}

.curved-section::after {
  z-index: -1;
  content: "";
  position: absolute;
  bottom: -200px;
  right: 0;
  height: 35%;
  width: 105%;
  background-color: black;
  border-radius: 0 0 100% 100%;
  
  
}

.show-graf{
  background-image: none;
}



/* For screens smaller than 480px (typical mobile size) */

@media (max-width: 1768px) {

  .inner-shadow {
    box-shadow: none;
  }

  .footer {
    height: auto;
  }

  .show-graf {
    background-image: url("./Res/bg.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .no-show {
    visibility: hidden;
    width: 0;
    height: 0;
  }
  .show {
    visibility: visible;
    height: auto;
    width: auto;
  }


  .no-padding {
    padding: 10px;
  }

  .curved-section::after {
    z-index: -1;
    content: "";
    position: absolute;
    bottom: -100px;
    left: -5vw;
    right: 0;
    height: 25%;
    width: 105%;
    background-color: black;
    border-radius: 0 0 100% 100%;
  }

  h1 {
    font-size: 26px;
  }

  .custom-padding {
    padding-bottom: 20px;
  }

  #Boxcontainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 80vw;
    height: 120px;
    background-color: white;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }

  .phone-card {
    height: 60vh;
  }

  .color {
    padding: 0%;
  }



}


