.Slider-BoxContainer {
  display: grid;
  grid-template-columns: repeat(3, 30%); /* Adjust width as needed */
  grid-template-rows: repeat(2, 280px); /* Adjust height as needed */
  gap: 50px; /* Adjust gap between grid items as needed */
  /* background-color: #fff; */
  padding: 10px;
}

.Slide {
  width: 15vw;
  padding: 20px;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  background-color: #f2f2f2;
  height: 100%;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
}

.web {
  height: 70%;
  width: 100%;
  background-image: url("../Res/web-settings_7990955.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

