.footer-main {
  display: flex;
  width: 100%;
  height: 100%;
}

.main-footer {
  height: 100%;
  width: 100%;
}

.footer-sub {
  display: flex;
  width: 50%;
}

.footer-flex {
  display: flex;
}

.padded-text {
  width: 50%;
  padding-top: 15px;
}

.padding {
  padding-bottom: 15px;
}

.highlight {
  font-size: 22px;
  font-weight: 400;
}

.link {
  background-image: url("../Res/social/LinkedIn.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.fb {
  background-image: url("../Res/social/Facebook.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.instagram {
  background-image: url("../Res/social/Instagram.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.threads {
  background-image: url("../Res/social/Threads\ 1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.tiktok {
  background-image: url("../Res/social/tiktok_5968812\ 1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.icon {
  margin: 1%;
  border-radius: 12px;
  width: 60px;
  height: 60px;
}


.footer-a{
  margin: 1%;
}
@media (max-width: 1768px) {
}

/* For screens smaller than 480px (typical mobile size) */
@media (max-width: 480px) {
  .footer-flex {
    display: block;
  }
  .footer-sub {
    display: flex;
    width: 100%;
  }

  .footer-main {
    display: block;
    width: 100%;
    height: 100%;
  }

  .icon, .footer-a {
    margin-right: 1%;
    margin-bottom: 3%;
    border-radius: 10px;
    width: 40px;
    height: 40px;
  }

  .padded-text {
    width: 80%;
    padding-top: 15px;
  }
}
