#box {
  height: 40vh;
  width: 60vw;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
  background-color: white;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 40% 60%;
  /* grid-template-columns: repeat(2, 50%); Adjust width as needed */
  grid-template-rows: repeat(1, 100%); /* Adjust height as needed */
}

#saly {
  height: 70%;
  width: 70%;
  background-image: url("../Res/card.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.circleBase {
  border-radius: 50%;
}

.heading {
  font-size: 40px;
  color: black;
  font-weight: 800;
  text-align: start;
}

.description {
  font-size: 16px;
  font-weight: 500;
  text-align: start;
}

.container {
  height: 100%;
  width: 100%;
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading,
.description {
  padding: 10px;
}


@media (max-width: 1280px) {
  .heading{
    font-size: 24px;
    padding: 0px;
    margin: 0;
  }

  .description {
    font-size: 14px;
    font-weight: 500;
    text-align: start;   
    padding: 0px;
    margin: 0;
  }

}



@media (max-width: 880px) {
  .heading{
    font-size: 28px;
    padding: 0px;
    margin: 0;
  }

  .description {
    font-size: 16px;
    font-weight: 500;
    text-align: start;   
    padding: 0px;
    margin: 0;
  }


  #box {
    height: 40vh;
    width: 100vw;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
    background-color: white;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 40% 60%;
    /* grid-template-columns: repeat(2, 50%); Adjust width as needed */
    grid-template-rows: repeat(1, 100%); /* Adjust height as needed */
  }

}



@media (max-width: 480px) {
  .heading{
    font-size: 18px;
    padding: 0px;
    margin: 0;
  }

  .description {
    font-size: 10px;
    font-weight: 500;
    text-align: start;   
    padding: 0px;
    margin: 0;
  }


  #box {
    height: 40vh;
    width: 100vw;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
    background-color: white;
    border-radius: 10px;
    display: grid;
    grid-template-columns: 40% 60%;
    /* grid-template-columns: repeat(2, 50%); Adjust width as needed */
    grid-template-rows: repeat(1, 100%); /* Adjust height as needed */
  }

}